
import { NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NotificationEngineCrudComponent } from './notification-engine-crud/notification-engine-crud.component';
import { TABS_DEMO_ROUTES } from './notification-engine-crud/routes';
import { AuthGuard } from 'app/shared/interceptors/auth-gaurd';
const routes : Routes = [
    {
        path : 'crud',component : NotificationEngineCrudComponent,canActivate: [AuthGuard], children : TABS_DEMO_ROUTES
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class NotificationEngineRoutingModule{}