import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Validators, FormBuilder, FormGroup, FormControl, FormArray, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Router } from '@angular/router';
import { PATTERNS } from 'app/shared/utils/validation-patterns';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { MessageConstant } from 'app/shared/utils/message.constant';
import { NotificationEngineService } from 'app/shared/services/notification-engine/notification-engine.service'
import { MenuMasterService } from 'app/shared/services/menu-master/menu-master.service';


function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.scss']
})


export class CreateComponent implements OnInit {

  form: FormGroup;
  localStorage: any;
  status:boolean = true;
  sendEmailChecked :boolean=false;
  sendSmsChecked :boolean=false;
  sendPushChecked :boolean=false;
  filteredOptionsProject : Observable<any[]>;
  project: any;
  @ViewChild("toggleElement") ref: any;
 
  
  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, private router: Router,
    private notificationService: NotificationEngineService,private menuMasterService:MenuMasterService) {

  }

  ngOnInit() {
    this.initialize(); 
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.getProject();
    this.form = this.fb.group({
      project: ['', [autocompleteObjectValidator(), Validators.required]],
      event_code: ['',[Validators.required,Validators.maxLength(50),]],
      event_name: ['',  [Validators.required,Validators.maxLength(100),]],
      send_email: ['', ],
      content_email: ['', [Validators.required,Validators.maxLength(3000)]],
      send_sms: ['', ],
      content_sms: ['', [Validators.required,Validators.maxLength(500)]],
      send_push:['',],
      content_push: ['', [Validators.required,Validators.maxLength(500)]],  
      status: this.status == true ? 'Y' : 'N'
    });
    this.form.get('content_email').disable();
    this.form.get('content_sms').disable();
    this.form.get('content_push').disable();


  }

  onSubmit() {
    if(this.sendEmailChecked == false ){
      this.form.get('content_email').setValue('');
      this.form.get('content_email').clearValidators();
    }else{
      this.form.get('content_email').setValidators(Validators.required);
    }

    if(this.sendSmsChecked == false ){
      this.form.get('content_sms').setValue('');
      this.form.get('content_sms').clearValidators();
    }else{
      this.form.get('content_sms').setValidators(Validators.required);
    }

    if(this.sendPushChecked == false ){
      this.form.get('content_push').setValue('');
      this.form.get('content_push').clearValidators();
    }else{
      this.form.get('content_push').setValidators(Validators.required);
    }

    if (this.form.valid) {
      let formValues = this.form.value;
      //console.log(this.form.value);
      let params = {
        project_id : formValues.project.id,
        event_code: formValues.event_code,
        event_name: formValues.event_name,
        send_email: this.sendEmailChecked === true ? 1 : 0, 
        send_sms: this.sendSmsChecked === true ? 1 : 0,
        send_push: this.sendPushChecked === true ? 1 : 0 ,
        content_email: formValues.content_email === undefined ? " " : formValues.content_email,
        content_sms: formValues.content_sms === undefined ? " " : formValues.content_sms,
        content_push: formValues.content_push === undefined ? " " : formValues.content_push,
        status: this.status == true ? 'Y' : 'N',
        created_by: this.localStorage.userId,
      }

      this.notificationService.createNotificationEngine(params).subscribe(
        (resp:any) => {
          if(resp.status == true){
            //console.log(params,resp.data);
            this.snackBar.open(MessageConstant.notificationCreate, 'Dismiss', { duration: 5000 });
            this.router.navigate(['/miscellaneous/notificationEngine/crud/list/']);
          }else{
            this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
          }

        },(err) =>{
          this.snackBar.open(MessageConstant.errorMessage, 'Dismiss', { duration: 5000 });
         }
      );
    }
    else {
      this.snackBar.open(MessageConstant.formInvalid, 'Dismiss', { duration: 5000 });
    }

  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  onCheckSendEmail(event){
    //console.log(event)
    if(event.checked){
      this.form.get('content_email').enable();
      this.form.get('content_email').setValidators(Validators.required);
      this.sendEmailChecked =true;
    }else{
      this.form.get('content_email').disable();
      this.form.get('content_email').setValue('');
      this.form.get('content_email').clearValidators();
      this.sendEmailChecked =false;
    }
  }
  onCheckSendSms(event){
    if(event.checked){
      this.form.get('content_sms').enable();
      this.form.get('content_sms').setValidators(Validators.required);
      this.sendSmsChecked =true;
    }else{
      this.form.get('content_sms').disable();
      this.form.get('content_sms').setValue('');
      this.form.get('content_sms').clearValidators();
      this.sendSmsChecked =false;
    }
  }
  onCheckSendPush(event){
    if(event.checked){
      this.form.get('content_push').enable();
      this.form.get('content_push').setValidators(Validators.required);
      this.sendPushChecked =true;
    }else{
      this.form.get('content_push').disable();
      this.form.get('content_push').setValue('');
      this.form.get('content_push').clearValidators();
      this.sendPushChecked =false;
    }
  }

  getProject(){
    this.menuMasterService.getProject().subscribe(
      (resp:any) =>{
        if(resp != null){
          this.project = resp;
          this.filteredOptionsProject = this.form.controls.project.valueChanges
          .pipe(
            startWith(''),
            map(value => typeof value === 'string' ? value : value.name),
            map(name => name ? this._filterProject(name) : this.project)
          );

        }
      },(err) => { }

    );
  }
  private _filterProject(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.project.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayProject(item: any): string{
    return item && item.name ? item.name : '';
  }




}


