import { Component, OnInit, Inject, ElementRef, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators, ValidatorFn, AbstractControl } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MtxDialogData } from '@ng-matero/extensions';
import { Router } from '@angular/router';
import { ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { NotificationEngineService } from 'app/shared/services/notification-engine/notification-engine.service';
import { MenuMasterService } from 'app/shared/services/menu-master/menu-master.service';
import { Observable } from 'rxjs/internal/Observable';
import { map, startWith } from 'rxjs/operators';

function autocompleteObjectValidator(): ValidatorFn {
  return (control: AbstractControl): { [key: string]: any } | null => {
    if (typeof control.value === 'string') {
      return { 'invalidAutocompleteObject': { value: control.value } }
    }
    return null  /* valid option selected */
  }
}

@Component({
  selector: 'app-view-dialog',
  templateUrl: './view-dialog.component.html',
  styleUrls: ['./view-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations: fuseAnimations
})
export class ViewDialogComponent implements OnInit {

  params: any;
  isEditMode = false;
  form: FormGroup;
  localStorage: any;
  status:boolean = true;
  sendEmailChecked :boolean=false;
  sendSmsChecked :boolean=false;
  sendPushChecked :boolean=false;
  @ViewChild("toggleElement") ref: any;
  project: any;
  filteredOptionsProject : Observable<any[]>;
  menuArray: any;
  constructor(private fb: FormBuilder, private snackBar: MatSnackBar, public dialogRef: MatDialogRef<ViewDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: MtxDialogData, private router: Router, private notificationService:NotificationEngineService,
    private menuMasterService:MenuMasterService) {

  }

  ngOnInit() {
    this.initialize();  
  }

  initialize() {
    this.localStorage = JSON.parse(localStorage.getItem('userDetails'));
    this.menuArray  = JSON.parse( localStorage.getItem('activeMenuItems'));
    this.params = this.data;
    //console.log(this.params)
    this.isEditMode = this.params.action == 'CREATE' ? false : true;

    this.form = this.fb.group({
      project: ['', [autocompleteObjectValidator(), Validators.required]],
      event_code: ['',[Validators.required,Validators.maxLength(50),Validators.pattern('^[a-zA-Z ]*$')]],
      event_name: ['',  [Validators.required,Validators.maxLength(100),Validators.pattern('^[a-zA-Z ]*$')]],
      send_email: ['', ],
      content_email: ['', [Validators.required,Validators.maxLength(3000)]],
      send_sms: ['', ],
      content_sms: ['', [Validators.required,Validators.maxLength(500)]],
      send_push:['',],
      content_push: ['', [Validators.required,Validators.maxLength(500)]],  
      status: this.status == true ? 'Y' : 'N'
    });
    this.form.get('send_email').disable();
    this.form.get('send_sms').disable();
    this.form.get('send_push').disable();

    if (this.isEditMode) {
      this.assignValues(this.params);
    }
    this.getProject();

  }

  close() {
    this.dialogRef.close();
  }
  closeActionPanel() {
    this.dialogRef.close();
  }

  getErrorMessage(form: FormGroup) {
    return form.get('email').hasError('required')
      ? 'You must enter a value'
      : form.get('email').hasError('email')
        ? 'Not a valid email'
        : '';
  }

  assignValues(params) {
    this.sendEmailChecked = this.params.data.send_email == 1 ? true :false;
    this.sendSmsChecked = this.params.data.send_sms == 1 ? true :false;
    this.sendPushChecked = this.params.data.send_push == 1 ? true :false;
    this.form.get('event_code').setValue(params.data.event_code);
    this.form.get('event_name').setValue(params.data.event_name);
    this.form.get('content_email').setValue(params.data.content_email);
    this.form.get('content_sms').setValue(params.data.content_sms);
    this.form.get('content_push').setValue(params.data.content_push);
    this.form.get('send_email').setValue(this.sendEmailChecked);
    this.form.get('send_sms').setValue(this.sendSmsChecked);
    this.form.get('send_push').setValue(this.sendPushChecked);
    this.status = this.params.data.status == 'Y' ? true : false;
  }

  updateStatus(event) {
    this.status = event.checked;
    this.ref.checked = this.status;
  }
  
  getProject() {
    this.menuMasterService.getProject().subscribe(
      (resp: any) => {
        if (resp != null) {
          this.project = resp;
          if(this.isEditMode){
              const toSelect = this.project.find(c => c.id == this.params.data.project_id);
              this.form.get('project').setValue(toSelect);
            }
           this.filteredOptionsProject = this.form.controls.project.valueChanges
            .pipe(
              startWith(''),
              map(value => typeof value === 'string' ? value : value.name),
              map(name => name ? this._filterProject(name) : this.project)
            );
        }
      }, (err) => { }

    );
  }
  private _filterProject(name: string): any[] {
    const filterValue = name.toLowerCase();
    return this.project.filter(option => option.name.toLowerCase().includes(filterValue));
  }
  displayProject(item: any): string{
    return item && item.name ? item.name : '';
  }
  


}










