
import {NgModule} from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes : Routes = [
    {
        path : 'evChargingSolution',
		loadChildren : () => import('./EV-charging-solution/EV-charging-solution.module').then(m=>m.EVChargingSolutionModule),
		data : {title : 'Miscellaneous'}
    },
    {
        path : 'notificationEngine',
        loadChildren : () => import('./notification-engine/notification-engine.module').then(m => m.NotificationEngineModule),
        data : {title : 'Miscellaneous'}
    }
]

@NgModule({
    imports : [RouterModule.forChild(routes)],
    exports : [RouterModule]
})

export class MiscellaneousRoutingModule{}